import React from 'react';
import { Comment, Form, Header, Button } from 'semantic-ui-react';

import moment from 'moment';

import person from './person.png';

function SingleComment(detail) {
   return (
      <Comment>
         <Comment.Content>
            <Comment.Avatar src={person} />
            <Comment.Author as="a">hongseok</Comment.Author>
            <Comment.Metadata>
               <div>{detail.info.time}</div>
            </Comment.Metadata>
            <Comment.Text>{detail.info.content}</Comment.Text>
            <Comment.Actions>
               <Comment.Action>Reply</Comment.Action>
            </Comment.Actions>
         </Comment.Content>
      </Comment>
   );
}

class Comments extends React.Component {
   constructor() {
      super();
      this.state = {
         inputContent: '',
         inputTime: '',
         commentsList: [],
      };
   }

   render() {
      return (
         <Comment.Group style={{ marginLeft: '600px' }}>
            <Header as="h3" dividing>
               Comments
            </Header>

            {this.state.commentsList.map((comments) => (
               <SingleComment info={comments} />
            ))}

            <Form reply>
               <Form.TextArea
                  value={this.state.inputContent}
                  placeholder="댓글을 입력해주세요!"
                  onChange={(e) =>
                     this.setState({ inputContent: e.target.value })
                  }
               />
               <Button
                  content="Add Reply"
                  labelPosition="left"
                  icon="edit"
                  primary
                  onClick={() => {
                     if (this.state.inputContent != '') {
                        this.setState((prevState) => {
                           return {
                              commentsList: [
                                 ...prevState.commentsList,
                                 {
                                    content: this.state.inputContent,
                                    time: moment().format('MMMM,DD,YYYY'),
                                 },
                              ],
                              inputContent: '',
                           };
                        });
                     } else {
                        alert('내용을 입력해 주세요!');
                     }
                  }}
               />
            </Form>
         </Comment.Group>
      );
   }
}

export default Comments;
